<template>
  <div>
    <div id = 'lockedpage_box'>
      <div id = 'locking' v-show = "style_flag">
        <div>
          <svg @click = "change_style_flag" t="1654506459417" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7894" width="150" height="150"><path d="M744 421h-9V273.1C735 171.59 652.41 89 550.9 89h-77.8C371.59 89 289 171.59 289 273.1V421h-9a84.25 84.25 0 0 0-84 84v340a84.25 84.25 0 0 0 84 84h464a84.25 84.25 0 0 0 84-84V505a84.25 84.25 0 0 0-84-84zM560 707.78L572 804H452l12-96.22a88 88 0 1 1 95.94 0zM655 421H369V273.1A104.22 104.22 0 0 1 473.1 169h77.8A104.22 104.22 0 0 1 655 273.1z" p-id="7895" fill="#515151"></path></svg>
          <p>请先登录</p>
        </div>
      </div>
      <div id = 'login_or_register' v-show = "!style_flag">
        <svg @click = "change_style_flag" t="1654509427996" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9047" width="25" height="25"><path d="M512 128c212 0 384 172 384 384s-172 384-384 384-384-172-384-384 172-384 384-384m0-64C264.8 64 64 264.8 64 512s200.8 448 448 448 448-200.8 448-448S759.2 64 512 64z m238.4 254.4l-45.6-45.6L512 467.2 318.4 273.6l-45.6 45.6L467.2 512 273.6 705.6l45.6 45.6L512 557.6l193.6 193.6 45.6-45.6L557.6 512l192.8-193.6z" p-id="9048" fill="#515151"></path></svg>
        <div id = 'login_register'>
          <div id = 'login_register_head'>
            <p @click = "change_login_register_flag(1)" :class = "{'head_choosed': login_register_flag}">登录</p>
            <p @click = "change_login_register_flag(2)" :class = "{'head_choosed': !login_register_flag}">注册</p>
          </div>
          <Login v-show = "login_register_flag"/>
          <Register v-if = "!login_register_flag"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { ref } from 'vue';
  import Login from './lockedpage/Login';
  import Register from './lockedpage/Register';
  export default {
  components: {Login, Register},
    setup(){
      // 处于锁界面还是登录注册界面
      let style_flag = ref(true);
      function change_style_flag(){
        style_flag.value = !style_flag.value;
      }
      // 处于登录界面还是注册界面
      let login_register_flag = ref(true);
      function change_login_register_flag(flag){
        if(flag === 1){
          login_register_flag.value = true;
        }
        else{
          login_register_flag.value = false;
        }
      }
      return {style_flag, login_register_flag, change_style_flag, change_login_register_flag};
    }
  }
</script>

<style scoped>
  /* 整个背景设置 */
  #lockedpage_box{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.2);
  }
  /* 界面所在位置 */
  #locking, #login_or_register{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
    height: 400px;
    width: 700px;
    background: rgb(220, 220, 220);
  }
  #locking{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: larger;
    user-select: none;
  }
  #locking > :first-child > :nth-child(1):hover{
    cursor: pointer;
  }
  #locking>:first-child > :nth-child(2):hover{
    cursor: default;
  }
  #login_or_register > :first-child{
    position: absolute;
    right: 5px;
    top: 5px;
    z-index: 1;
  }
  #login_or_register > :first-child:hover{
    cursor: pointer;
  }
  #login_register{
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 320px;
    width: 300px;
    background: white;
    bottom: 0;
    top: 0;
    right: 10px;
    margin-top: auto;
    margin-bottom: auto;
  }
  #login_register_head{
    display: flex;
    color: rgb(180, 180, 180);
    width: 150px;
    font-size: larger;
    justify-content: space-between;
    user-select: none;
    margin-top: 30px;
  }
  #login_register_head :nth-child(n):hover{
    cursor: pointer;
  }
  .head_choosed{
    color: rgb(50, 50, 50);
  }
</style>