<template>
  <div>
    <div id = "fun_data">
      <div class = "fun_data_item" v-for = "item, index in fun_data" :key = "index">
        <p>{{index}}</p>
        <p>{{item}}</p>
      </div>
      <div id = "fun_data_chart"></div>
    </div>
  </div>
</template>

<script>
  import * as echarts from 'echarts';
  import { onMounted } from 'vue'
  function bind(box_name, option){
    var myChart = echarts.init(document.getElementById(box_name));
    myChart.setOption(option);
  }
  let fun_option = {
    title: {
      text: '用户访问量'
    },
    xAxis: {
      data: ['06-03', '06-04', '06-05', '06-06', '06-07', '06-08', '06-09']
    },
    yAxis: {},
    series: [
      {
        name: '用户访问量',
        type: 'line',
        data: [10, 5, 20, 36, 10, 10, 20]
      },
      {
        name: '活跃用户',
        type: 'line',
        data: [4, 3, 17, 16, 1, 4, 2]
      }
    ]
  };

  export default {
    setup(){
      onMounted(() => {
          bind("fun_data_chart", fun_option);
      })
      let fun_data = {
        "关注数": "xxx",
        "粉丝数": "xxx",
      }
      return {
        fun_data
      }
    }
  }
</script>

<style scoped>
  #fun_data{
    display: flex;
    width: 1000px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .fun_data_item{
    width: 300px;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #fun_data_chart{
    width: 1000px;
    height: 400px;
  }
</style>