<template>
  <div>
    <div id = "edit_box" style = "height: 100vh">
      <div id = "edit_head">
         <el-button type="primary" @click = "exit">返回</el-button>
         <el-input v-model="blog.title" placeholder="请输入标题" maxlength="50" show-word-limit></el-input>
         <el-button type="primary" @click = "change_publish_blog_flag">保存文章</el-button>
      </div>
      <MdEditor v-model.lazy = "blog.article"></MdEditor>
    </div>
    <div v-if = "publish_blog_flag" id = "publish_blog_box">
      <div id = "publish_blog_form">
        <el-form :model="blog" label-width="120px">
          <!-- 当form中只存在一个input时，按enter会自动提交form，所以这里添加一个不显示的input（下面那个input实质是textarea） -->
          <el-form-item>
            <input id="hiddenText" type="text" style="display:none" />
          </el-form-item>
          <el-form-item label="文章标签">
             <el-tag
              v-for="tag, index in blog.labels"
              :key="tag"
              closable
              :disable-transitions="false"
              @close="blog.labels.splice(index, 1)"
            >
              {{ tag }}
            </el-tag>
            <el-input
              v-if="input_visible"
              ref="input_ref"
              v-model="input_value"
              class="ml-1 w-20"
              style="width: 72px; height: 24px"
              size="small"
              @keyup.enter="handle_input_confirm"
              @blur="handle_input_confirm"
            />
            <el-button v-else class="button-new-tag ml-1" size="small" @click="show_input">
              添加标签
            </el-button>
          </el-form-item>
          <el-form-item label="是否发布">
            <el-switch v-model="blog.flag" />
          </el-form-item>
          <el-form-item label="文章摘要">
            <el-input v-model="blog.message" type="textarea" maxlength="256" show-word-limit rows="5" />
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submit">完成</el-button>
            <el-button @click="change_publish_blog_flag">取消</el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
  import MdEditor from 'md-editor-v3';
  import 'md-editor-v3/lib/style.css';
  import { inject, ref, nextTick } from 'vue';
  import { useRoute, useRouter } from 'vue-router';
  export default {
    components:{
      MdEditor
    },
    setup(){
      const axios = inject('axios');
      const $route = useRoute();
      const $router = useRouter();
      const blog = ref({});
      if($route.query.hasOwnProperty('id')){
        // 获取文章内容
        let a = new Promise((resolve, reject) => { 
          axios.get(`/api/user/blog/information?id=${$route.query.id}`).then(value => {
            value.data.flag = value.data.flag ? true : false;
            resolve(value.data);
          })
        });
        let b = new Promise((resolve, reject) => {
          // 获取文章标签
          axios.get(`/api/user/blog/label?id=${$route.query.id}`).then(value => {
            let labels = [];
            for(let i = 0; i < value.data.length; i++){
              labels.push(value.data[i].label);
            }
            resolve(labels);
          })
        });
        Promise.all([a, b]).then(value => {
          blog.value = {...value[0], labels: value[1]};
        })
      }
      else{
        blog.value = {
          message: "",
          title: "",
          labels: [],
          article: "",
          flag: true,
          id: 0 // 辨别是添加博客还是编辑博客
        }
      }
      // 是否出现发布表单
      const publish_blog_flag = ref(false);
      function change_publish_blog_flag(){
        publish_blog_flag.value = !publish_blog_flag.value;
      }
      // 用来得到input的值
      const input_value = ref("");
      // 是否显示input框
      const input_visible = ref(false);
      // 标记input框的ref属性
      const input_ref = ref();
      // 展示输入框，用于添加文章标签
      const show_input = () => {
        input_visible.value = true;
        // 出现后焦点自动在输入框上
        nextTick(() => {
          input_ref.value.input.focus()
        })
        return false;
      }
      // 添加标签，并将输入框初始化和不显示
      const handle_input_confirm = () => {
        if (input_value.value) {
          blog.value.labels.push(input_value.value)
        }
        input_visible.value = false
        input_value.value = ''
      }
      // 退出编辑界面
      function exit(){
        $router.push('/manage/home_page/blog_manage');
      }
      // 提交表单
      function submit(){
        // 添加博客
        if(blog.value.id === 0){
          axios.post('/api/user/blog/add', { blog: blog.value }).then((value) => {
            alert(value.data);
            $router.push('/manage/home_page/blog_manage');
          })
        }
        else{
          axios.post('/api/user/blog/edit', { blog: blog.value }).then((value) => {
            console.log(value.data);
            alert(value.data);
            $router.push('/manage/home_page/blog_manage');
          })
        }
      }
      return { blog, publish_blog_flag, change_publish_blog_flag, show_input, input_ref, handle_input_confirm, input_value, input_visible, exit, submit };
    }
  }
</script>

<style scoped>
  #edit_box{
    display: flex;
    flex-direction: column;
    padding: 10px;
  }
  #edit_box > :last-child{
    flex: 1;
  }
  #edit_head{
    display: flex;
  }
  #edit_head > *{
    margin: 10px;
  }
  #publish_blog_box{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }

  #publish_blog_form{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: 100px auto;
    width: 600px;
    height: 300px;
    padding: 60px;
    background-color: rgb(255, 255, 255);
  }
</style>