<template>
  <div>
    <div>
      <div id = "blog_manage_head">
        <el-button type="primary" round @click="add_blog">添加博客</el-button>
      </div>
      <div id = "blog_list">
        <div class = "blog_list_box" v-for = "item, index in blogs" :key = "item.id">
          <div class = "blog_list_show">
            <div class = "blog_list_title">{{item.title.substring(0, 35)}}</div>
            <div class = "blog_list_message">{{item.message.substring(0, 50)}}</div>
            <div class = "blog_list_foot">
              <div class = "blog_list_foot_son">
                <span>xxx</span>
                <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"></path><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"></path></svg>
              </div>
              <div class = "blog_list_foot_son">
                <span>xxx</span>
                <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M4.318 6.318a4.5 4.5 0 000 6.364L12 20.364l7.682-7.682a4.5 4.5 0 00-6.364-6.364L12 7.636l-1.318-1.318a4.5 4.5 0 00-6.364 0z"></path></svg>
              </div>
              <div class = "blog_list_foot_son">
                <span>xxx</span>
                <svg class="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 10h.01M12 10h.01M16 10h.01M9 16H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-5l-5 5v-5z"></path></svg>
              </div>
            </div>
          </div>
          <div class = "blog_list_publish">
            <input class = "blog_list_publish_check" type = "checkbox" @click = "change_blog_flag(index)" :checked = "!blogs[index].flag"/>
            <a>发布</a>
          </div>
          <div class = "blog_list_set">
            <el-button type="primary" plain @click="edit_blog(index)">编辑</el-button>
            <el-button type="danger" plain @click="delete_blog(index)">删除</el-button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { inject, ref } from 'vue';
  import { useRouter } from 'vue-router';
  export default {
    setup(){
      const $router = useRouter()
      const axios = inject('axios');
      const blogs = ref([]);
      const blogs_flag = ref([]);
      // 获取博客信息
      axios.get(`/api/user/blog/get`).then(value => {
        blogs.value = value.data;
      })
      // 获取所有标签
      axios.get(`/api/user/blog/label`).then(value => {
        for(let i = 0; i < blogs.value.length; i++){
          blogs.value[i].label = [];
          for(let j = 0; j < value.data.length; j++){
            if(blogs.value[i].id === value.data[j].id){
              blogs.value[i].label.push(value.data[j].label);
            }
          }
        }
      })
      // 修改博客是否发布的状态
      function change_blog_flag(index){
        blogs.value[index].flag = blogs.value[index].flag ^ 1;
        axios.get(`/api/user/blog/set?id=${blogs.value[index].id}&flag=${blogs.value[index].flag}`);
      }
      // 跳转到添加博客界面
      function add_blog(){
        $router.push(`/edit`);
      }
      // 跳转到编辑博客界面
      function edit_blog(index){
        $router.push(`/edit?id=${blogs.value[index].id}`);
      }
      // 删除指定博客
      function delete_blog(index){
        axios.get(`/api/user/blog/delete?id=${blogs.value[index].id}`).then(value => {
          if(value.data === '删除成功'){
            blogs.value.splice(index, 1);
            blogs_flag.value.splice(index, 1);
          }
          alert(value.data);
        })
      }      
      return { blogs, change_blog_flag, blogs_flag, add_blog, edit_blog, delete_blog };
    }
  }
</script>

<style scoped>
  #blog_manage_head{
    margin-top: 20px;
    width: 1000px;
    margin-right: auto;
    margin-left: auto;
    display: flex;
    justify-content: flex-end;
  }
  #blog_list{
    width: 1000px;
    margin-right: auto;
    margin-left: auto;
  }
  #blog_list, .blog_list_box, .blog_list_show, .blog_list_set{
    display: flex;
  }
  .blog_list_box{
    align-items: center;
    height: 130px;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 5px 5px 5px rgb(175, 175, 175);
    border-width: 1px;
    border-style: solid;
    border-color: rgb(233, 233, 233);
  }
  .blog_list_box > :first-child{
    margin-left: 20px;
  }
  .blog_list_box > :last-child{
    margin-right: 20px;
  }
  .blog_list_show{
    flex: 1;
    height: 80%;
  }
  .blog_list_title{
    font-size: 20px;
  }
  .blog_list_message{
    font-size: 14px;
    color: grey;
    margin-top: 10px;
    flex: 1;
  }
  .blog_list_foot, .blog_list_foot_son{
    display: flex;
    align-items: center;
  }
  .blog_list_foot_son{
    width: 80px;
  }
  .blog_list_foot_son > svg{
    margin-left: 5px;
    width:20px;
    height: 20px;
  }
  .blog_list_foot_son:hover{
    cursor: default;
  }
  #blog_list, .blog_list_show, .blog_list_set{
    flex-direction: column;
  }
  .blog_list_publish{
    margin-right: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 14px;
  }
  .blog_list_publish > :last-child{
    color: grey;
    cursor: default;
  }
  .blog_list_publish_check{
    appearance: none;
    width: 50px;
    height: 25px;
    border-radius: 12.5px;
    background: rgb(100, 201, 100);
    margin-bottom: 7px;
    transition-duration: 100ms;
  }
  .blog_list_publish_check:checked{
    background: rgb(128, 128, 128);
  }
  .blog_list_publish_check:hover{
    cursor: pointer;
  }
  .blog_list_publish_check::before{
    content: "";
    display: block;
    margin-top: 2.5px;
    margin-left: 26px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: white;
    transition-duration: 100ms;
  }
  .blog_list_publish_check:checked::before{
    transform: translateX(-22px);
  }
  .blog_list_set > *{
    margin-bottom: 10px;
  }
  .blog_list_set > :last-child{
    margin-bottom: 0;
  }
  /* 默认有个值，会造成对不齐 */
  .el-button+.el-button{
    margin-left: 0;
  }
</style>