<template>
  <div>
    <div id = "user_set">
      <div id = "user_photo">
        <img src = "data:image/png;base64,">
        <el-button type="primary" icon="Edit" style="width:100px">修改头像</el-button>
      </div>
      <div id = "user_sex">
        <span>性别：</span>
        <el-radio-group v-model="user_information_new.user_sex" style="width:150px">
          <el-radio-button label="男"></el-radio-button>
          <el-radio-button label="女"></el-radio-button>
          <el-radio-button label="保密"></el-radio-button>
        </el-radio-group>
      </div>
      <div id = "user_name">
        <span>用户名：</span>
        <el-input v-model="user_information_new.user_name" placeholder="请输入内容" style="width:150px"></el-input>
      </div>
      <div id = "user_id">
        <span>用户id：</span>
        <el-input
          placeholder="请输入内容"
          v-model="user_information_new.user_id"
          :disabled="true"
          style="width:180px">
        </el-input>
      </div>
      <div id = "user_password">
        <span>密码：</span>
        <el-input placeholder="请输入密码" v-model="user_information_new.user_password" show-password style="width:200px"></el-input>
      </div>
      <div id = "user_email">
        <span>邮箱：</span>
        <el-input
          placeholder="请输入内容"
          v-model="user_information_new.user_email"
          :disabled="true"
          style="width:200px">
        </el-input>
      </div>
      <div id = "user_registered">
        <span>注册时间：</span>
        <el-date-picker
          disabled
          v-model="user_information_new.user_registered"
          type="date"
          placeholder="选择日期"
          style="width:150px">
        </el-date-picker>
      </div>
      <div id = "user_birth">
        <span>生日：</span>
        <el-date-picker
          v-model="user_information_new.user_birth"
          type="date"
          placeholder="选择日期"
          style="width:150px">
        </el-date-picker>
      </div>
      <div id = "user_introduction">
        <span>个人简介：</span>
        <el-input
          type="textarea"
          placeholder="请输入内容"
          v-model="user_information_new.user_introduction"
          maxlength="30"
          show-word-limit
          rows="3"
          resize="none"
          style="width:890px"
        >
        </el-input>
      </div>
      <div id = "save">
        <el-button type="primary" @click = "reset">重置</el-button>
        <el-button type="primary" @click = "save">保存</el-button>
      </div>
    </div>
  </div>
</template>

<script>
  import { inject, ref, toRaw } from 'vue';
  export default {
    setup(){
      const axios = inject('axios');
      let user_information_old;
      let user_information_new = ref({});
      axios.get('/api/user/information').then(value => {
        user_information_old = value.data;
        user_information_new.value = {...user_information_old};
      })
      function reset(){
        user_information_new.value = {...user_information_old};
      }
      function save(){
        // 先将proxy对象转换为普通对象
        axios.post('/api/user/modify', toRaw(user_information_new.value)).then(value => {
          alert(value.data);
        })
      }
      return { user_information_new, reset, save }
    }
  }
</script>

<style>
  #user_set{
    width: 1000px;
    height: 600px;
    margin-right: auto;
    margin-left: auto;
    display: grid;
    white-space:nowrap;
    grid-template-rows: repeat(auto-fill, 10px);
    grid-template-columns: repeat(auto-fill, 10px);
  }
  #user_set > *{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  #user_photo{
    grid-area: 4 / 1 / 10 / 26;
  }
  #user_sex{
    grid-area: 4 / 51 / 10 / 73;
  }
  #user_name{
    grid-area: 11 / 1 / 17/ 25;
  }
  #user_id{
    grid-area: 11 / 51 / 17 / 78;
  }
  #user_password{
    grid-area: 18 / 1 / 24 / 28;
  }
  #user_email{
    grid-area: 18 / 51 / 24 / 78;
  }
  #user_registered{
    grid-area: 25 / 1 / 31 / 27;
  }
  #user_birth{
    grid-area: 25 / 51 / 31 / 73;
  }
  #user_introduction{
    align-items: flex-start;
    grid-area: 35 / 1 / 40 / 101;
  }
  #save{
    grid-area: 52 / 1 / 55 / 101;
    justify-content: center;
  }
  #save > :first-child{
    margin-right: 30px;
  }
</style>