<template>
  <div>
    <div id = "input_box">
      <div class = 'input_row'>
        <p>邮箱：</p>
        <el-input v-model="user_email" placeholder="请输入内容" style="width:180px" clearable></el-input>
      </div>
      <div class = 'input_row'>
        <p>用户名：</p>
        <el-input v-model="user_name" placeholder="请输入内容" style="width:180px" clearable></el-input>
      </div>
      <div class = 'input_row'>
        <p>密码：</p>
        <el-input placeholder="请输入密码" type="password" v-model="user_password_1" show-password style="width:180px"></el-input>
      </div>
      <div class = 'input_row'>
        <p>确认密码：</p>
        <el-input placeholder="请输入密码" type="password" v-model="user_password_2" show-password style="width:180px"></el-input>
      </div>
      <button @click = "register">确认</button>
    </div>
  </div>
</template>

<script>
  import { ref, inject } from 'vue'
  export default {
    setup(){
      const axios = inject('axios');
      const user_email = ref('');
      const user_name = ref('');
      const user_password_1 = ref('');
      const user_password_2 = ref('');
      function register(){
        if(user_password_1.value === user_password_2.value !== '')
        {
          axios.post('/api/user/register', {
            user_email: user_email.value,
            user_name: user_name.value,
            user_password: user_password_1.value
          }).then(value => {
              alert(value.data);
            }
          )
        }
      }
      return {
        user_email, user_name, user_password_1, user_password_2, register
      } 
    }
  }
</script>

<style scoped>
  #input_box{
    margin-top: 15px;
  }
  .input_row{
    margin-top: 12px;
    display: flex;
  }
  .input_row p{
    margin-left: 12px;
    width: 80px;
    color: grey;
    user-select: none;
    line-height: 32px;
  }
  .input_row p:hover{
    cursor: default;
  }
  #input_box button{
    position: absolute;
    width: 50px;
    height: 30px;
    bottom: 20px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
  #input_box button:hover{
    cursor: pointer;
  }
</style>