import { createApp } from 'vue';
import App from './App.vue';
import router from './router';
import axios from 'axios';
import VueAxios from 'vue-axios';

import { ElInput, ElRadio, ElRadioGroup, ElRadioButton, ElDatePicker, ElButton, ElForm, ElSwitch, ElTag } from 'element-plus';
import 'element-plus/theme-chalk/el-input.css';
import 'element-plus/theme-chalk/el-radio.css';
import 'element-plus/theme-chalk/el-radio-group.css';
import 'element-plus/theme-chalk/el-radio-button.css';
import 'element-plus/theme-chalk/el-date-picker.css';
import 'element-plus/theme-chalk/el-button.css';
import 'element-plus/theme-chalk/el-form.css';
import 'element-plus/theme-chalk/el-switch.css';
import 'element-plus/theme-chalk/el-tag.css';
import 'element-plus/theme-chalk/base.css';
import * as ElementPlusIconsVue from '@element-plus/icons-vue'


const app = createApp(App);

// 使用element的icon
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
  app.component(key, component)
}

// 使用element组件
app.use(ElInput);
app.use(ElRadio);
app.use(ElRadioGroup);
app.use(ElRadioButton);
app.use(ElDatePicker);
app.use(ElButton);
app.use(ElForm);
app.use(ElSwitch);
app.use(ElTag);

// 初始化axios
const API = axios.create({
  baseURL: 'http://1.116.146.54:1218', //请求后端数据的基本地址，自定义
  timeout: 2000                    //请求超时设置，单位ms
});

//在连接端口之前，通过axios拦截器添加token，保证拥有获取数据的权限.
API.interceptors.request.use(config => {
  //为请求头对象添加token验证的Authorization字段
  config.headers.Authorization = window.localStorage.getItem('Token');
  return config;
})

app.use(VueAxios, API);
app.provide('axios', API);

app.use(router);
app.mount('#app');