<template>
  <div id = "homepage_box">
    <Bar id = 'bar' v-show = "bar_flag" ref = "bar"/>
    <Upper 
      id = 'upper' 
      :style = "{ width: bar_flag ? '82%' : '100%' }" 
      :bar_flag = "bar_flag"
      @change_bar_flag = "change_bar_flag" 
      :theme_flag = "theme_flag"
      @change_theme_flag = "change_theme_flag"/>
    <router-view id = "pages" :style = "{left: bar_flag ? '18%' : '0', width: bar_flag ? '82%' : '100%'}" ></router-view>
  </div>
</template>

<script>
  import { ref } from 'vue';
  import Bar from './homepage/Bar';
  import Upper from './homepage/Upper';
  export default {
    components: {
      Bar, Upper
    },
    setup() {
      // 标记导航栏是否展开
      let bar_flag = ref(true);
      // 切换导航栏是否展开
      function change_bar_flag(){
        bar_flag.value = !bar_flag.value;
      }
      // 标记页面样式
      let theme_flag = ref(0);
      let theme_class = ['theme_sun', 'theme_moon'];
      // 切换页面样式
      function change_theme_flag(){
        theme_flag.value = (theme_flag.value + 1) % theme_class.length;
      }
      return {bar_flag, change_bar_flag, theme_flag, change_theme_flag};
    }
  }
</script>

<style scoped>
  #bar{
    position: fixed;
    width: 18%;
    background: rgb(37, 77, 116);
    left: 0;
    top: 0;
    bottom: 0;
  }
  #upper{
    position: fixed;
    height: 50px;
    background: rgb(61, 128, 194);
    right: 0;
    top: 0;
    z-index: 1;
  }
  #pages{
    position: absolute;
    top: 50px;
  }
</style>
