import { createRouter, createWebHashHistory } from "vue-router";
import Manage from '../components/manage/Manage.vue'
import HomePage from '../components/manage/HomePage.vue'
import BlogData from '../components/manage/homepage/pages/BlogData.vue';
import BlogManage from '../components/manage/homepage/pages/BlogManage.vue';
import UserSet from '../components/manage/homepage/pages/UserSet.vue';
import FunData from '../components/manage/homepage/pages/FunData.vue';
import LockedPage from '../components/manage/LockedPage.vue'
import Edit from '../components/edit/Edit.vue'

const routes = [
  {
    path: '/',
    redirect: '/manage/locked_page'
  },
  {
    path: '/manage',
    component: Manage,
    children: [
      {
        path: 'home_page',
        redirect: '/manage/home_page/blog_manage',
        component: HomePage,
        children:[
          {
            path: 'blog_data',
            component: BlogData
          },
          {
            path: 'blog_manage',
            component: BlogManage
          },
          {
            path: 'user_set',
            component: UserSet
          },
          {
            path: 'fun_data',
            component: FunData
          }
        ]
      },
      {
        path: 'locked_page',
        component: LockedPage
      }
    ]
  },
  {
    path: '/edit',
    name: 'edit',
    component: Edit
  }
];

export default createRouter({
  routes,
  history: createWebHashHistory()
})


