<template>
  <div>
    <div id = "blog_data">
      <div class = "blog_data_item" v-for = "item, index in blog_data" :key = "index">
        <p>{{index}}</p>
        <p>{{item}}</p>
      </div>
      <div id = "blog_data_chart"></div>
    </div>
  </div>
</template>

<script>
  import * as echarts from 'echarts';
  import { onMounted } from 'vue'
  export default {
    setup(){
      onMounted(() => {
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById('blog_data_chart'));
        // 指定图表的配置项和数据
        var option = {
          title: {
            text: 'test'
          },
          tooltip: {},
          legend: {
            data: ['销量']
          },
          xAxis: {
            data: ['衬衫', '羊毛衫', '雪纺衫', '裤子', '高跟鞋', '袜子']
          },
          yAxis: {},
          series: [
            {
              name: '销量',
              type: 'bar',
              data: [5, 20, 36, 10, 10, 20]
            }
          ]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      })
      let blog_data = {
        "博客总数": "xxx",
        "点赞数": "xxx",
        "收藏数": "xxx",
        "评论数": "xxx",
        "阅读量": "xxx",
      }
      return {
        blog_data
      }
    }
  }
</script>

<style scoped>
  #blog_data{
    display: flex;
    width: 1000px;
    margin-right: auto;
    margin-left: auto;
    text-align: center;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  .blog_data_item{
    width: 300px;
    height: 150px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  #blog_data_chart{
    width: 1000px;
    height: 400px;
  }
</style>