<template>
  <div>
    <div id = "input_box">
      <div class = 'input_row'>
        <p>用户名：</p>
        <el-input v-model="user_name" placeholder="请输入内容" clearable style="width:180px"></el-input>
      </div>
      <div class = 'input_row'>
        <p>密码：</p>
        <el-input placeholder="请输入密码" type="password" v-model="user_password" show-password style="width:180px"></el-input>
      </div>
      <button @click = "login_func">确认</button>
    </div>
  </div>
</template>

<script>
  import { inject, ref } from 'vue'
  import { useRouter } from 'vue-router'
  export default {
    setup(){
      const $router=useRouter()
      const axios = inject('axios');
      const user_name = ref('');
      const user_password = ref('');
      function login_func(){
        axios.post('/api/user/login/', {
          user_name: user_name.value,
          user_password: user_password.value
        }).then(value => {
          if(value.data === '登录失败'){
            alert("用户名或密码出错");
          }
          else{
            alert("登录成功");
            // （数据库中的null是字符串）
            // 修改获取数据中的头像，当数据库中没对应头像时，使用默认头像
            const default_photo = 'data:image/png;base64,';
            if(value.data.data.user_photo === 'null'){
              value.data.data.user_photo = default_photo;
            }
            // 修改获取数据中的自我介绍，当数据库中没对应自我介绍时，使用默认介绍
            const default_introduction = ''
            if(value.data.data.user_introduction === 'null'){
              value.data.data.user_introduction = default_introduction;
            }
            // 第一次登录时，在localStorage中添加token，之后每次请求添加token
            window.localStorage.setItem('Token', value.data.token);
            $router.push({ path: '/manage/home_page' })
          }
      })
      }
      return {user_name, user_password, login_func};
    }
  }
</script>

<style scoped>
  #input_box{
    margin-top: 50px;
  }
  .input_row{
    margin-top: 30px;
    display: flex;
  }
  .input_row p{
    margin-left: 12px;
    width: 80px;
    color: grey;
    user-select: none;
    line-height: 32px;
  }
  .input_row p:hover{
    cursor: default;
  }
  #input_box button{
    position: absolute;
    width: 50px;
    height: 30px;
    bottom: 20px;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
  }
  #input_box button:hover{
    cursor: pointer;
  }
</style>